// src/components/settings/Settings.tsx

import React, { useState } from 'react';
import {
    Box,
    Typography,
    Button,
    Grid,
    Divider,
    Snackbar,
    Card,
    CardContent,
    CardHeader,
    List,
    ListItem,
    ListItemText,
    Switch,
    FormControlLabel,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText,
    DialogActions
} from '@mui/material';
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import {
    CloudDownload as DownloadIcon,
    CloudUpload as UploadIcon,
    Delete as DeleteIcon,
    Warning as WarningIcon
} from '@mui/icons-material';

import { storageService } from '../../services/storage.service';
import { scheduleService } from '../../services/schedule.service';
import { taskService } from '../../services/task.service';
import { evaluationService } from '../../services/evaluation.service';
import { studentService } from '../../services/student.service';
import { userService } from '../../services/user.service';

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
    props,
    ref,
) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const Settings: React.FC = () => {
    const [message, setMessage] = useState<{ text: string, severity: 'success' | 'error' | 'info' } | null>(null);
    const [resetDialogOpen, setResetDialogOpen] = useState(false);
    const [importSuccessOpen, setImportSuccessOpen] = useState(false);
    const [exportSuccessOpen, setExportSuccessOpen] = useState(false);

    // Exporter toutes les donnes de l'application
    const handleExportData = () => {
        try {
            // Collecter toutes les donnes  exporter
            const exportData = {
                user: storageService.getData('user'),
                students: storageService.getData('students'),
                tasks: storageService.getData('tasks'),
                taskCategories: storageService.getData('taskCategories'),
                evaluations: storageService.getData('evaluations'),
                rubrics: storageService.getData('rubrics'),
                studentInternships: storageService.getData('studentInternships'),
                regularSchedule: storageService.getData('regularSchedule'),
                holidays: storageService.getData('holidays'),
                specialEvents: storageService.getData('specialEvents'),
                internshipPeriods: storageService.getData('internshipPeriods'),
                alternatingWeeks: storageService.getData('alternatingWeeks'),
                // Ajoutez d'autres donnes si ncessaire
                exportDate: new Date().toISOString(),
                appVersion: '1.0.0' // Vous pouvez stocker une version pour grer les migrations futures
            };

            // Convertir en chane JSON
            const jsonString = JSON.stringify(exportData, null, 2);

            // Crer un Blob pour le tlchargement
            const blob = new Blob([jsonString], { type: 'application/json' });
            const url = URL.createObjectURL(blob);

            // Crer un lien de tlchargement
            const a = document.createElement('a');
            a.href = url;
            a.download = `teachquest_backup_${new Date().toISOString().split('T')[0]}.json`;
            document.body.appendChild(a);
            a.click();

            // Nettoyer
            document.body.removeChild(a);
            URL.revokeObjectURL(url);

            setExportSuccessOpen(true);
        } catch (error) {
            console.error('Erreur lors de l\'exportation des donnes:', error);
            setMessage({ text: 'Erreur lors de l\'exportation des donnes', severity: 'error' });
        }
    };

    // Importer des donnes
    const handleImportData = (event: React.ChangeEvent<HTMLInputElement>) => {
        const file = event.target.files?.[0];
        if (!file) return;

        const reader = new FileReader();
        reader.onload = (e) => {
            try {
                const content = e.target?.result as string;
                const importedData = JSON.parse(content);

                // Vrification de base pour s'assurer que c'est un fichier valide
                if (!importedData || typeof importedData !== 'object') {
                    throw new Error('Format de fichier invalide');
                }

                // Importer chaque section de donnes
                Object.entries(importedData).forEach(([key, value]) => {
                    // Ignorer la date d'exportation et la version de l'app
                    if (key !== 'exportDate' && key !== 'appVersion') {
                        storageService.setData(key, value);
                    }
                });

                // Recharger tous les services
                scheduleService.reloadData();
                taskService.reloadData();
                studentService.reloadData();
                evaluationService.reloadData();
                userService.reloadData();

                setImportSuccessOpen(true);
            } catch (error) {
                console.error('Erreur lors de l\'importation des donnes:', error);
                setMessage({ text: 'Erreur lors de l\'importation des donnes: format invalide', severity: 'error' });
            }
        };

        reader.readAsText(file);

        // Rinitialiser le champ de fichier pour permettre une nouvelle importation du mme fichier
        event.target.value = '';
    };

    // Rinitialiser toutes les donnes
    const handleResetData = () => {
        try {
            // Effacer toutes les donnes
            localStorage.clear();

            // Recharger la page pour rinitialiser l'application
            window.location.reload();
        } catch (error) {
            console.error('Erreur lors de la rinitialisation:', error);
            setMessage({ text: 'Erreur lors de la rinitialisation des donnes', severity: 'error' });
        }
    };

    return (
        <Box sx={{ p: 2 }}>
            <Typography variant="h4" gutterBottom>
                Paramtres
            </Typography>

            <Grid container spacing={3}>
                {/* Gestion des donnes */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardHeader title="Gestion des donnes" />
                        <CardContent>
                            <Grid container spacing={2} direction="column">
                                <Grid item>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        startIcon={<DownloadIcon />}
                                        onClick={handleExportData}
                                        fullWidth
                                    >
                                        Exporter toutes les donnes
                                    </Button>
                                </Grid>

                                <Grid item>
                                    <Button
                                        variant="outlined"
                                        component="label"
                                        startIcon={<UploadIcon />}
                                        fullWidth
                                    >
                                        Importer des donnes
                                        <input
                                            type="file"
                                            accept=".json"
                                            hidden
                                            onChange={handleImportData}
                                        />
                                    </Button>
                                </Grid>

                                <Grid item>
                                    <Divider sx={{ my: 2 }} />
                                </Grid>

                                <Grid item>
                                    <Button
                                        variant="outlined"
                                        color="error"
                                        startIcon={<DeleteIcon />}
                                        onClick={() => setResetDialogOpen(true)}
                                        fullWidth
                                    >
                                        Rinitialiser toutes les donnes
                                    </Button>
                                    <Typography variant="caption" color="text.secondary" sx={{ mt: 1, display: 'block' }}>
                                        Attention: Cette action effacera dfinitivement toutes vos donnes.
                                    </Typography>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>

                {/* Autres paramtres pourront tre ajouts ici */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardHeader title="Prfrences utilisateur" />
                        <CardContent>
                            <List>
                                <ListItem>
                                    <ListItemText
                                        primary="Thme sombre"
                                        secondary="Changer l'apparence de l'application"
                                    />
                                    <FormControlLabel
                                        control={
                                            <Switch
                                                // Vous pourriez connecter ceci  un tat global de thme
                                                onChange={(e) => console.log('Theme toggle:', e.target.checked)}
                                            />
                                        }
                                        label=""
                                    />
                                </ListItem>

                                <ListItem>
                                    <ListItemText
                                        primary="Notifications"
                                        secondary="Activer les notifications pour les chances"
                                    />
                                    <FormControlLabel
                                        control={
                                            <Switch
                                                // Vous pourriez connecter ceci  un paramtre utilisateur
                                                onChange={(e) => console.log('Notifications toggle:', e.target.checked)}
                                            />
                                        }
                                        label=""
                                    />
                                </ListItem>
                            </List>
                        </CardContent>
                    </Card>
                </Grid>

                {/*  propos */}
                <Grid item xs={12}>
                    <Card>
                        <CardHeader title=" propos de TeachQuest" />
                        <CardContent>
                            <Typography variant="body1" paragraph>
                                TeachQuest est une application conue pour les enseignants afin de grer leurs tches, emplois du temps et valuations.
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                Version: 1.0.0
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>

            {/* Dialogue de confirmation pour rinitialiser les donnes */}
            <Dialog
                open={resetDialogOpen}
                onClose={() => setResetDialogOpen(false)}
                aria-labelledby="reset-dialog-title"
                aria-describedby="reset-dialog-description"
            >
                <DialogTitle id="reset-dialog-title" sx={{ display: 'flex', alignItems: 'center' }}>
                    <WarningIcon color="error" sx={{ mr: 1 }} />
                    Confirmer la rinitialisation
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="reset-dialog-description">
                        Vous tes sur le point de supprimer toutes les donnes de l'application. Cette action est irrversible.
                        <br /><br />
                        Nous vous recommandons vivement d'exporter vos donnes avant de procder  la rinitialisation.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setResetDialogOpen(false)} autoFocus>
                        Annuler
                    </Button>
                    <Button onClick={handleResetData} color="error" variant="contained">
                        Rinitialiser
                    </Button>
                </DialogActions>
            </Dialog>

            {/* Dialogue de succs d'importation */}
            <Dialog
                open={importSuccessOpen}
                onClose={() => setImportSuccessOpen(false)}
                aria-labelledby="import-success-dialog-title"
            >
                <DialogTitle id="import-success-dialog-title">
                    Importation russie
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Les donnes ont t importes avec succs. Vous pouvez maintenant utiliser ces donnes dans l'application.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setImportSuccessOpen(false)} autoFocus>
                        OK
                    </Button>
                </DialogActions>
            </Dialog>

            {/* Dialogue de succs d'exportation */}
            <Dialog
                open={exportSuccessOpen}
                onClose={() => setExportSuccessOpen(false)}
                aria-labelledby="export-success-dialog-title"
            >
                <DialogTitle id="export-success-dialog-title">
                    Exportation russie
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Les donnes ont t exportes avec succs. Vous pouvez maintenant utiliser ce fichier pour sauvegarder ou transfrer vos donnes.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setExportSuccessOpen(false)} autoFocus>
                        OK
                    </Button>
                </DialogActions>
            </Dialog>

            {/* Notification pour les erreurs */}
            {message && (
                <Snackbar
                    open={true}
                    autoHideDuration={6000}
                    onClose={() => setMessage(null)}
                    anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
                >
                    <Alert
                        onClose={() => setMessage(null)}
                        severity={message.severity}
                        sx={{ width: '100%' }}
                    >
                        {message.text}
                    </Alert>
                </Snackbar>
            )}
        </Box>
    );
};

export default Settings;