// src/utils/evaluationExportUtils.ts

import { Evaluation, Student, Rubric, RubricCriterion } from '../types';
import { evaluationService } from '../services/evaluation.service';

/**
 * Gnre un export CSV dtaill des rsultats d'valuation avec tous les critres du barme
 * @param evaluation L'valuation  exporter
 * @param students Liste des tudiants concerns
 * @param includeEmptyRows Inclure ou non les lves sans note
 * @returns Contenu CSV format
 */
export const generateDetailedEvaluationGrid = (
    evaluation: Evaluation,
    students: Student[],
    includeEmptyRows: boolean = false
): string => {
    // Rcuprer le barme de l'valuation
    const rubric = evaluationService.getRubricById(evaluation.rubricId);
    if (!rubric) {
        console.error("Barme non trouv pour l'valuation:", evaluation.id);
        return "Erreur: Barme introuvable";
    }

    // Crer les en-ttes de colonnes
    const headers = ['Nom', 'Prnom'];

    // Tableau pour stocker les identifiants des colonnes des critres (pour les utiliser dans les donnes)
    const criteriaColumnIds: string[] = [];

    // Ajouter les critres et sous-critres comme colonnes
    rubric.criteria.forEach(criterion => {
        if (criterion.subCriteria && criterion.subCriteria.length > 0) {
            // Si le critre a des sous-critres, ajouter une colonne pour chaque sous-critre
            criterion.subCriteria.forEach(subCriterion => {
                const columnId = `${criterion.id}_${subCriterion.id}`;
                criteriaColumnIds.push(columnId);
                headers.push(`${criterion.name} - ${subCriterion.name} (${subCriterion.points}pts)`);
            });
        } else {
            // Sinon, ajouter seulement le critre principal
            criteriaColumnIds.push(criterion.id);
            headers.push(`${criterion.name} (${criterion.points}pts)`);
        }
    });

    // Ajouter les colonnes de total et de commentaire
    headers.push('Total', 'Note /20', 'Commentaires');

    // Commencer le CSV avec les en-ttes
    let csv = headers.join(',') + '\n';

    // Rcuprer tous les tudiants concerns
    const relevantStudents = students.filter(student => {
        // Si on inclut les lignes vides, prendre tous les tudiants
        if (includeEmptyRows) return true;

        // Sinon, prendre seulement ceux qui ont une note
        const copy = evaluation.copies.find(c => c.studentId === student.id);
        return copy && copy.grade !== null;
    });

    // Ajouter les donnes pour chaque tudiant
    relevantStudents.forEach(student => {
        const studentCopy = evaluation.copies.find(c => c.studentId === student.id);

        // Si l'tudiant n'a pas de copie et qu'on n'inclut pas les lignes vides, passer
        if (!studentCopy && !includeEmptyRows) return;

        // chapper les noms et prnoms pour viter les problmes avec les virgules
        const escapeName = (name: string) => `"${name.replace(/"/g, '""')}"`;

        // Commencer la ligne avec le nom et prnom
        const row = [escapeName(student.lastName), escapeName(student.firstName)];

        // Ajouter les points pour chaque critre/sous-critre
        criteriaColumnIds.forEach(columnId => {
            if (!studentCopy || studentCopy.grade === null) {
                row.push(''); // Pas de note
                return;
            }

            // Dterminer s'il s'agit d'un critre principal ou d'un sous-critre
            const [criterionId, subCriterionId] = columnId.split('_');

            // Trouver le dtail du critre dans la copie
            const criterionDetail = studentCopy.details?.find(d => d.id === criterionId);

            if (!criterionDetail) {
                row.push('0');
                return;
            }

            if (subCriterionId) {
                // C'est un sous-critre
                const subCriterionDetail = criterionDetail.subCriteria?.find(sc => sc.id === subCriterionId);
                row.push(subCriterionDetail ? subCriterionDetail.points.toString() : '0');
            } else {
                // C'est un critre principal sans sous-critres
                row.push(criterionDetail.points.toString());
            }
        });

        // Ajouter le total des points
        const total = studentCopy && studentCopy.grade !== null ? studentCopy.grade : '';
        row.push(total.toString());

        // Ajouter la note sur 20
        const gradeOn20 = studentCopy && studentCopy.grade !== null
            ? ((studentCopy.grade / evaluation.maxPoints) * 20).toFixed(2)
            : '';
        row.push(gradeOn20);

        // Ajouter les commentaires (chapps pour le CSV)
        const comments = studentCopy && studentCopy.comments
            ? `"${studentCopy.comments.replace(/"/g, '""').replace(/\n/g, ' ')}"`
            : '';
        row.push(comments);

        // Ajouter la ligne au CSV
        csv += row.join(',') + '\n';
    });

    return csv;
};

/**
 * Tlcharge un fichier CSV contenant toutes les donnes de l'valuation
 * @param evaluation L'valuation  exporter
 * @param students Liste des tudiants
 * @param includeEmptyRows Inclure ou non les lves sans note
 */
export const downloadDetailedEvaluationGrid = (
    evaluation: Evaluation,
    students: Student[],
    includeEmptyRows: boolean = false
): void => {
    try {
        // Gnrer le contenu CSV
        const csvContent = generateDetailedEvaluationGrid(
            evaluation,
            students,
            includeEmptyRows
        );

        // Crer un nom de fichier bas sur l'valuation
        const fileName = `${evaluation.name.replace(/\s+/g, '_')}_grille_complete_${new Date().toISOString().split('T')[0]}.csv`;

        // Crer un Blob avec le contenu CSV
        const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });

        // Crer un lien de tlchargement
        const link = document.createElement('a');

        // Grer les diffrentes mthodes de tlchargement selon les navigateurs
        if ((navigator as any).msSaveBlob) {
            // Pour IE et Edge
            (navigator as any).msSaveBlob(blob, fileName);
        } else {
            // Pour les autres navigateurs
            const url = URL.createObjectURL(blob);
            link.href = url;
            link.setAttribute('download', fileName);
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            URL.revokeObjectURL(url);
        }
    } catch (error) {
        console.error("Erreur lors de l'export de la grille d'valuation:", error);
        throw new Error("Impossible d'exporter la grille d'valuation. Veuillez ressayer.");
    }
};