// src/services/task.service.ts

import { v4 as uuidv4 } from 'uuid';
import { Task, TaskCategory } from '../types';
import { format } from 'date-fns';
import { storageService } from './storage.service';

export class TaskService {
    // Mthode pour recharger les donnes
    public reloadData(): void {
        try {
            // Vrification simple en essayant d'accder aux donnes
            const tasks = this.getAllTasks();
            const categories = this.getTaskCategories();
            
            // Si les tches n'existent pas, crer un tableau vide
            if (!tasks || tasks.length === undefined) {
                storageService.setData('tasks', []);
            }
            
            // Si les catgories n'existent pas, essayer de les rcuprer depuis le stockage global
            if (!categories || categories.length === 0) {
                const taskCategories = storageService.getData('taskCategories');
                if (taskCategories && taskCategories.length > 0) {
                    storageService.setData('taskCategories', taskCategories);
                }
            }
        } catch (error) {
            console.error("Erreur lors du rechargement des donnes de tches:", error);
            
            // Rinitialiser les tches en cas d'erreur critique
            try {
                storageService.setData('tasks', []);
            } catch {
                // Si mme la rinitialisation choue, il y a un problme critique avec le stockage
            }
        }
    }

    // Rcuprer toutes les tches
    getAllTasks(): Task[] {
        try {
            return storageService.getCollection<Task>('tasks');
        } catch (error) {
            console.error("Erreur lors de la rcupration des tches:", error);
            return [];
        }
    }
    
    // Rcuprer une tche par son ID
    getTaskById(id: string): Task | undefined {
        try {
            const tasks = this.getAllTasks();
            return tasks.find(task => task.id === id);
        } catch (error) {
            console.error(`Erreur lors de la rcupration de la tche ${id}:`, error);
            return undefined;
        }
    }
    
    // Rcuprer les tches  venir
    getUpcomingTasks(days: number = 7): Task[] {
        try {
            const tasks = this.getAllTasks();
            const today = new Date();
            const futureDate = new Date();
            futureDate.setDate(today.getDate() + days);
            
            return tasks.filter(task => {
                if (task.status === 'completed') {
                    return false;
                }
                
                const dueDate = new Date(task.dueDate);
                return dueDate >= today && dueDate <= futureDate;
            }).sort((a, b) => new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime());
        } catch (error) {
            console.error("Erreur lors de la rcupration des tches  venir:", error);
            return [];
        }
    }
    
    // Rcuprer les tches pour une date spcifique
    getTasksForDate(date: Date): Task[] {
        try {
            const tasks = this.getAllTasks();
            const dateStr = format(date, 'yyyy-MM-dd');
            
            return tasks.filter(task => {
                return task.dueDate === dateStr;
            });
        } catch (error) {
            console.error(`Erreur lors de la rcupration des tches pour ${date}:`, error);
            return [];
        }
    }
    
    // Rcuprer toutes les catgories de tches
    getTaskCategories(): TaskCategory[] {
        try {
            return storageService.getCollection<TaskCategory>('taskCategories');
        } catch (error) {
            console.error("Erreur lors de la rcupration des catgories:", error);
            return [];
        }
    }
    
    // Calculer les points XP pour une tche selon la formule  3 dimensions
    calculateTaskPoints(baseXP: number, estimeValue: number, plaisirValue: number): number {
        const settings = {
            baseXpMultiplier: 1.0,
            estimeMultiplier: 0.7,
            plaisirAdjustment: -0.3
        };
        
        // Points = XP * (1 + (Estime * estimeMultiplier/10) - (Plaisir * plaisirAdjustment/10))
        const points = baseXP * (
            1 + 
            (estimeValue * settings.estimeMultiplier / 10) - 
            (plaisirValue * settings.plaisirAdjustment / 10)
        );
        
        return Math.round(points);
    }
    
    // Crer une nouvelle tche
    createTask(taskData: Partial<Task>): Task {
        try {
            const taskCategories = this.getTaskCategories();
            const taskCategory = taskCategories.find(cat => cat.id === taskData.categoryId);
            
            if (!taskCategory) {
                throw new Error('Catgorie de tche non trouve');
            }
            
            // Calculer les points XP
            const earnedXP = this.calculateTaskPoints(
                taskCategory.xpValue,
                taskCategory.estimeValue,
                taskCategory.plaisirValue
            );
            
            const newTask: Task = {
                id: uuidv4(),
                name: taskData.name || 'Nouvelle tche',
                description: taskData.description || '',
                categoryId: taskData.categoryId || 0,
                status: 'pending',
                dueDate: taskData.dueDate || format(new Date(), 'yyyy-MM-dd'),
                relatedGroups: taskData.relatedGroups || [],
                relatedSubject: taskData.relatedSubject || '',
                priority: taskData.priority || 3,
                estimatedTime: taskData.estimatedTime || 0,
                earnedXP: earnedXP,
                notes: taskData.notes || ''
            };
            
            // Sauvegarder la tche avec gestion d'erreur
            const tasks = this.getAllTasks();
            tasks.push(newTask);
            
            try {
                storageService.setCollection('tasks', tasks);
            } catch (error) {
                console.error("Erreur lors de la sauvegarde de la tche:", error);
                
                // Tentative de rcupration: sauvegarder uniquement la nouvelle tche
                try {
                    storageService.setCollection('tasks', [newTask]);
                } catch {
                    throw new Error("Impossible de sauvegarder la tche. Espace de stockage insuffisant.");
                }
            }
            
            return newTask;
        } catch (error) {
            console.error("Erreur lors de la cration de la tche:", error);
            throw error;
        }
    }
    
    // Mettre  jour une tche existante
    updateTask(taskData: Task): Task {
        try {
            const tasks = this.getAllTasks();
            const index = tasks.findIndex(task => task.id === taskData.id);
            
            if (index === -1) {
                throw new Error('Tche non trouve');
            }
            
            tasks[index] = taskData;
            
            try {
                storageService.setCollection('tasks', tasks);
            } catch (error) {
                console.error("Erreur lors de la mise  jour des tches:", error);
                throw new Error("Impossible de sauvegarder les modifications. Espace de stockage insuffisant.");
            }
            
            return taskData;
        } catch (error) {
            console.error("Erreur lors de la mise  jour de la tche:", error);
            throw error;
        }
    }
    
    // Complter une tche
    completeTask(taskId: string): Task {
        try {
            const task = this.getTaskById(taskId);
            
            if (!task) {
                throw new Error('Tche non trouve');
            }
            
            const updatedTask: Task = {
                ...task,
                status: 'completed',
                completionDate: format(new Date(), 'yyyy-MM-dd')
            };
            
            return this.updateTask(updatedTask);
        } catch (error) {
            console.error("Erreur lors de la compltion de la tche:", error);
            throw error;
        }
    }
    
    // Supprimer une tche
    deleteTask(taskId: string): void {
        try {
            const tasks = this.getAllTasks();
            const filteredTasks = tasks.filter(task => task.id !== taskId);
            storageService.setCollection('tasks', filteredTasks);
        } catch (error) {
            console.error("Erreur lors de la suppression de la tche:", error);
            throw error;
        }
    }
    
    // Sauvegarder toutes les tches
    saveAllTasks(tasks: Task[]): boolean {
        try {
            return storageService.setCollection('tasks', tasks);
        } catch (error) {
            console.error("Erreur lors de la sauvegarde de toutes les tches:", error);
            return false;
        }
    }
}

export const taskService = new TaskService();