// src/services/user.service.ts

import { storageService } from './storage.service';
import { User } from '../types';

class UserService {
  // Mthode pour recharger les donnes
  public reloadData(): void {
    // Pas besoin de faire quoi que ce soit de spcial car toutes les mthodes
    // vont directement chercher les donnes dans le storageService
  }

  // Rcuprer le profil utilisateur
  getUserProfile(): User | null {
    return storageService.getUserProfile();
  }
  
  // Sauvegarder le profil utilisateur
  saveUserProfile(user: User): boolean {
    return storageService.saveUserProfile(user);
  }
  
  // Ajouter des points XP
  addXP(points: number): User | null {
    const user = this.getUserProfile();
    
    if (!user) {
      console.error("Profil utilisateur non trouv");
      return null;
    }
    
    // Ajouter les points
    const newXP = user.xp + points;
    
    // Vrifier si passage de niveau
    let newLevel = user.level;
    let newXpForNextLevel = user.xpForNextLevel;
    
    if (newXP >= user.xpForNextLevel) {
      newLevel += 1;
      newXpForNextLevel = this.calculateXpForNextLevel(newLevel);
    }
    
    // Mettre  jour les statistiques
    const updatedStats = {
      ...user.stats,
      totalXpEarned: user.stats.totalXpEarned + points
    };
    
    // Mettre  jour l'utilisateur
    const updatedUser: User = {
      ...user,
      xp: newXP,
      level: newLevel,
      xpForNextLevel: newXpForNextLevel,
      stats: updatedStats
    };
    
    // Sauvegarder les modifications
    this.saveUserProfile(updatedUser);
    
    return updatedUser;
  }
  
  // Calculer les XP ncessaires pour le niveau suivant
  calculateXpForNextLevel(level: number): number {
    // Formule: 100 * niveau + 50 * (niveau - 1)^2
    return 100 * level + 50 * Math.pow(level - 1, 2);
  }
  
  // Complter une tche et ajouter les XP correspondants
  completeTask(taskId: string, earnedXP: number): User | null {
    // Mettre  jour les statistiques de tches
    const user = this.getUserProfile();
    
    if (!user) {
      console.error("Profil utilisateur non trouv");
      return null;
    }
    
    // Mettre  jour le nombre de tches compltes
    const updatedStats = {
      ...user.stats,
      tasksCompleted: user.stats.tasksCompleted + 1
    };
    
    // Mettre  jour l'utilisateur
    const updatedUser: User = {
      ...user,
      stats: updatedStats
    };
    
    // Sauvegarder les modifications
    this.saveUserProfile(updatedUser);
    
    // Ajouter les XP
    return this.addXP(earnedXP);
  }
  
  // Obtenir l'tat actuel de progression
  getProgressionStatus(): {
    level: number;
    currentXP: number;
    xpForNextLevel: number;
    progress: number;
    badges: string[];
  } {
    const user = this.getUserProfile();
    
    if (!user) {
      return {
        level: 1,
        currentXP: 0,
        xpForNextLevel: 100,
        progress: 0,
        badges: []
      };
    }
    
    const progress = (user.xp / user.xpForNextLevel) * 100;
    
    return {
      level: user.level,
      currentXP: user.xp,
      xpForNextLevel: user.xpForNextLevel,
      progress,
      badges: user.badges
    };
  }
}

export const userService = new UserService();
export default userService;